@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
*{
  box-sizing: border-box;
  margin:0;
  padding: 0;
}

:root{
  --input-font-color:  #676767;
  --input-bg-color: #c8d5b9;
  --primary-color: #4a7c59;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.app{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 400px;
  margin: 0 auto;
  padding: 10px;
}

.title{
  margin: 5px;
  font-size: 3em;
}


.input-field {
  display: block;
  padding: 10px;
  margin: 5px;
  height: 45px;
  border: solid 1px black;
  border-radius: 8px;
  max-width: 203px;
  background-color: #c8d5b9;
  color: var(--input-font-color);
}


.submit-btn {
  border: solid 1px black;
  height: 54px;
  max-width: 203px;
  border-radius: 60px;
  background-color: var(--primary-color);
  /* color: var(--input-font-color); */
  vertical-align: middle;
}


img {
  width: 100%;
  height: auto;
}

.a-btn{
  font-weight: 700;
  font-size: 1.2em;
}


.register-form {
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: space-around;
  padding: 10px;
  min-height: 300px;
  max-width: 50vh;
}


.register{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  max-height: 700px;
}

.register-logo-container{
  position: relative;
  left: 150px;
  bottom: 300px;
  width: 357px;
  height: 50px;
  z-index: -1;
}

.prompt-login-user{
  font-size: 19px;
  font-weight: 520;
  position: relative;
  left: 20px;
  bottom: 50px;
}

.LoginForm {
  display: flex;
  flex-direction: column;
  align-self: center;
  justify-content: space-around;
  padding: 10px;
  min-height: 200px;
  max-width: 50vh;
}



.login{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  max-height: 700px;
}



.login-logo-container{
  position: relative;
  left: 30px;
  width: 357px;
  height: 191px;
  z-index: -1;
}

.prompt-create-user{
  font-size: 19px;
  font-weight: 520;
  position: relative;
  left: 20px;
  bottom: 50px;
}

.donate-form {
  display: flex;
  height: 500px;
  flex-direction: column;
  align-self: center;
  padding: 10px;
  max-width: 320px;
}

.text-field {
  display: block;
  padding: 10px;
  margin: 5px;
  height: 150px;
  border: solid 1px black;
  border-radius: 8px;
  max-width: 203px;
  /* background-color: var(--input-bg-color); */
  color: var(--input-font-color);
}

.select-list {
  display: block;
  padding: 10px;
  margin: 5px;
  height: 45px;
  border: solid 2px black;
  border-radius: 8px;
  width: 95%;
  max-width: 203px;
  background-color: white;
  color: var(--input-font-color);
}

.donate-address{
  background-color: white;
}
.donate-separator{
  margin: 10px 0;
}

.donate-label{
  margin: 5px;
  color: var(--input-font-color);
}

.donate{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  max-height: 700px;
}

.back-arrow{
  margin-right: 200px;
  background: transparent;
  border: none;
  font-size: 2em;
  color: var(--primary-color);
}
.choose{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
  max-height: 700px;
}

.choose-btn{
  width: 100%;
}
